/* Estimation.css */

/* General Styling */
body {
  font-family: "Arial", sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f9f9f9;
}

.estimation-container {
  max-width: 800px;
  margin: 2rem auto;
  padding: 2rem;
  background: #ffffff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

/* Header Section */
.estimation-header {
  text-align: center;
  margin-bottom: 2rem;
}

.estimation-header h1 {
  font-size: 2.5rem;
  color: #2e7d32; /* Green */
  margin-bottom: 0.5rem;
}

.estimation-header p {
  font-size: 1.2rem;
  color: #555;
}

/* Form Styling */
.estimation-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.estimation-form label {
  font-size: 1rem;
  color: #333;
  margin-bottom: 0.3rem;
}

.estimation-form input {
  padding: 0.8rem;
  font-size: 1rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  outline: none;
}

.cta-button {
  padding: 0.8rem;
  font-size: 1rem;
  background-color: #2e7d32; /* Green */
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.cta-button:hover {
  background-color: #1b5e20; /* Dark Green */
}

/* Estimate Summary Card */
.estimate-card {
  margin-top: 2rem;
  padding: 1.5rem;
  background-color: #e8f5e9; /* Light Green Background */
  border: 1px solid #c8e6c9;
  border-radius: 8px;
  text-align: center;
}

.estimate-card h2 {
  font-size: 1.8rem;
  color: #2e7d32;
}

.estimate-card p {
  font-size: 1rem;
  color: #333;
  margin: 0.5rem 0;
}

.cta-button-secondary {
  margin-top: 1rem;
  padding: 0.8rem;
  font-size: 1rem;
  background-color: #f44336; /* Red */
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.cta-button-secondary:hover {
  background-color: #d32f2f; /* Darker Red */
}
