/* BlogUploadEdit.css */
.container1 {
  max-width: 800px;
  margin: auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

h2 {
  text-align: center;
  color: #333;
}

.message {
  text-align: center;
  color: #28a745;
  font-weight: bold;
}

.blog-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.blog-form input,
.blog-form textarea {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
}

.blog-form textarea {
  resize: none;
  height: 100px;
}

.form-buttons {
  display: flex;
  gap: 10px;
}

.form-buttons .cancel-button {
  background-color: #ffc107;
  color: #fff;
}

button {
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: #fff;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

.blogs-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.card {
  padding: 15px;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.card-buttons {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

.card-buttons .delete-button {
  background-color: #dc3545;
}
