:root {
  --primary-green: #164b1e; /* Main green color */
  --accent-red: #e74c3c; /* Red for warnings and errors */
  --text-dark: #2d3436;
  --text-light: #ffffff;
}

body {
  font-family: "Arial", sans-serif;
  margin: 0;
  background-color: #f8f9fa;
}

h1,
h2,
h3 {
  color: var(--text-dark);
}

/* Navbar Styles */
.navbar {
  background: var(--primary-green);
  color: var(--text-light);
  padding: 20px 20px;
  position: relative;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-logo {
  margin: 0;
  color: white;
  font-size: 1.5rem;
}

/* Hamburger Menu Icon */
.hamburger {
  display: none;
  flex-direction: column;
  gap: 4px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 10px;
}

.hamburger-line {
  width: 25px;
  height: 3px;
  background: var(--text-light);
  border-radius: 2px;
}

/* Navbar Links */
.navbar-links {
  display: flex;
  gap: 20px;
}

.navbar-link {
  color: white;
  text-decoration: none;
  font-size: 1rem;
}

.navbar-link:hover {
  text-decoration: underline;
}

/* Responsive Design */
@media (max-width: 768px) {
  .hamburger {
    display: flex;
  }

  .navbar-links {
    position: absolute;
    top: 60px;
    right: 0;
    flex-direction: column;
    background: var(--primary-green);
    padding: 20px;
    gap: 15px;
    border-radius: 4px;
    transform: translateX(100%);
    transition: transform 0.3s ease;
  }

  .navbar-links.open {
    transform: translateX(0);
  }

  .navbar-link {
    font-size: 1.2rem;
  }
}
