/* Blog.css */

body {
  font-family: "Arial", sans-serif;
  background-color: #f8f9fa;
  margin: 0;
  padding: 0;
}

.blog-container {
  max-width: 900px;
  margin: 2rem auto;
  padding: 2rem;
  background: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

header {
  text-align: center;
  margin-bottom: 2rem;
}

header h1 {
  font-size: 2rem;
  color: #343a40;
}

header p {
  color: #6c757d;
}

/* Success/Error Message */
.message {
  text-align: center;
  padding: 0.8rem;
  color: #ffffff;
  background-color: #28a745;
  border-radius: 5px;
  margin-bottom: 1rem;
}

/* Form Section */
.form-section h2 {
  color: #343a40;
  margin-bottom: 1rem;
}

.blog-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.blog-form input,
.blog-form textarea {
  padding: 0.8rem;
  font-size: 1rem;
  border: 1px solid #ced4da;
  border-radius: 5px;
  outline: none;
}

.blog-form textarea {
  height: 100px;
  resize: none;
}

.submit-button {
  padding: 0.8rem;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.submit-button:hover {
  background-color: #0056b3;
}

/* Blogs Section */
.blogs-section {
  margin-top: 2rem;
}

.blogs-section h2 {
  color: #343a40;
  margin-bottom: 1rem;
}

.blogs-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.blog-card {
  background: #f1f3f5;
  border: 1px solid #e9ecef;
  border-radius: 5px;
  padding: 1rem;
  position: relative;
}

.blog-card h3 {
  margin: 0 0 0.5rem;
  font-size: 1.2rem;
}

.blog-card p {
  margin: 0 0 1rem;
  color: #495057;
}

.delete-button {
  padding: 0.5rem;
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.delete-button:hover {
  background-color: #c82333;
}
