/* Home.css */

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f9f9f9;
}

.home-container {
  width: 100%;
  overflow-x: hidden;
}

/* Hero Section */
.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 5%;
  background-color: #e8f5e9; /* Light green background */
}

.hero-content {
  max-width: 50%;
}

.hero-title {
  font-size: 2.5rem;
  font-weight: bold;
  color: #2e7d32; /* Dark green */
}

.highlight {
  color: #66bb6a; /* Brighter green */
}

.hero-description {
  font-size: 1.2rem;
  margin-top: 1rem;
  color: #555;
}

.cta-button {
  margin-top: 1.5rem;
  padding: 0.8rem 2rem;
  font-size: 1rem;
  background-color: #2e7d32;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.hero-image {
  max-width: 40%;
  border-radius: 10px;
}

/* Features Section */
.features {
  padding: 2rem 5%;
  background-color: white;
}

.section-title {
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  color: #2e7d32;
  margin-bottom: 1.5rem;
}

.features-grid {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  flex-wrap: wrap;
}

.feature-card {
  flex: 1;
  min-width: 220px;
  background-color: #e8f5e9;
  padding: 1.5rem;
  border-radius: 8px;
  text-align: center;
  color: #2e7d32;
}

.feature-card h3 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

/* Testimonials Section */
.testimonials {
  padding: 2rem 5%;
  background-color: #f1f8e9; /* Soft green background */
}

.testimonials-grid {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.testimonial-card {
  padding: 1.5rem;
  background-color: white;
  border: 1px solid #c8e6c9;
  border-radius: 8px;
}

.testimonial-card p {
  font-size: 1rem;
  color: #555;
  margin-bottom: 0.5rem;
}

.testimonial-card h4 {
  text-align: right;
  font-size: 1rem;
  color: #2e7d32;
}

/* Call-to-Action Form Section */
.cta-form-section {
  padding: 2rem 5%;
  background-color: white;
  text-align: center;
}

.cta-form {
  max-width: 500px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.cta-form input,
.cta-form textarea {
  padding: 0.8rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  width: 100%;
}

.cta-form button {
  padding: 0.8rem;
  font-size: 1rem;
  background-color: #2e7d32;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
